import UserMemberContainerWrapper from "../../../Ui/UserMember/UserMemberContainerWrapper";
import UserMemberContentWrapper from "../../../Ui/UserMember/UserMemberContentWrapper";
import { useEffect, useState } from "react";
import { useTranslation } from "react-i18next";
import { useInView } from "react-intersection-observer";
import style from "../FinanceManager.module.css";
import BorderBottom from "../../../Ui/Border/BorderBottom";
import {
  assestsNavbarUrl,
  // assestsUserFinanceUrl,
} from "../../../Services/UrlConfig/AssestsBaseUrl";
// import { customDebounce } from "../../../Utils/Functions/debounce";
import { nextPage } from "../../../Utils/Functions/nextPage";
import {
  useFinanceAdmissionFeesStudentExport,
  useInstituteAllStudent,
} from "../../../Hooks/ApiHooks/FinanceManager/finance-manager-api";
import FinanceAllStudentTableHeader from "./FinanceAllStudentTableHeader";
import FinanceAllStudentCard from "./FinanceAllStudentCard";
// import CreateButton from "../../../Ui/Button/CreateButton";
// import AllStudentMessageSend from "./StudentMessageHistory/AllStudentMessageSend";
// import { useLocation, useNavigate, useParams } from "react-router-dom";
import QvipleLoader from "../../../Loader/QvipleLoader";
import Loading from "../../../Loader/Loading";
import EmptyMessage from "../../../Utils/EmptyMessage/EmptyMessage";
import CreateButton from "../../../Ui/Button/CreateButton";
import { useLocation, useNavigate, useParams } from "react-router-dom";

const FinanceAllStudent = ({
  instituteId,
  studentAlias,
  headId,
  messageId,
  // accessAs,
  // subUrl,
  invalidateAccess,
  // cancelledAuthorized,
  // otherFlow,
  allStudentControl,
  whichFlow,
  otherData,
}) => {
  const { t } = useTranslation();
  const navigate = useNavigate();
  const getQuery = useLocation();
  const params = useParams();
  // const [open, setOpen] = useState(false);
  const [search, setSearch] = useState("");
  const [searchCopy, setSearchCopy] = useState("");

  const [page, setPage] = useState(1);
  const [state, setState] = useState(true);
  const [ref, inView] = useInView({ skip: search });

  const [disabled, setDisabled] = useState(false);

  const [financeAdmissionFeesStudentExport] =
    useFinanceAdmissionFeesStudentExport();

  const { instituteAllStudent, instituteAllStudentLoading } =
    useInstituteAllStudent({
      data: {
        id: instituteId,
        page: page,
        limit: 10,
        search: search,
      },
      skip: !instituteId,
    });
  useEffect(() => {
    if (inView && state) setPage((page) => page + 1);
  }, [inView, state]);
  useEffect(() => {
    if (instituteAllStudent?.list) {
      let obj = nextPage(instituteAllStudent?.list?.length);
      setState(() => obj);
    }
  }, [instituteAllStudent?.list]);

  // const onDebounce = useCallback(
  //   customDebounce((val) => {
  //     setPage(() => 1);
  //     setSearch(val);
  //   }, 1500),
  //   []
  // );

  const onSearchEvent = (e) => {
    // if (e.target.value) {
    //   if (!instituteAllStudentLoading) onDebounce(e.target.value);
    // } else {
    //   setPage(() => 1);
    //   setSearch("");
    // }

    if (!instituteAllStudentLoading && instituteAllStudent?.list?.length >= 0) {
      // if (e.target.value) {
      setSearchCopy(e.target.value);
      // } else {
      //   // setSearchCopy("");
      //   // setSearch("");
      // }
    }
  };
  const onCallBindSearch = (e) => {
    if (e.key === "Enter") {
      setPage(() => 1);
      setSearch(searchCopy);
    }
  };
  // const onToggle = () => {
  //   setOpen((pre) => !pre);
  // };

  // const onNavigate = () => {
  //   navigate(`/q/${params.username}/member/staff/${subUrl}?type=message`, {
  //     state: getQuery.state,
  //   });
  // };

  const onExportList = () => {
    if (otherData?.aid) {
      setDisabled((pre) => !pre);
      financeAdmissionFeesStudentExport({
        aid: otherData?.aid,
      })
        .then((res) => {
          let url =
            getQuery.state?.urlOffSet === "Institute"
              ? `/q/${params.username}/institute/member/finance?type=export`
              : `/q/${params.username}/member/staff/finance?type=export`;
          navigate(url, {
            state: getQuery.state,
          });
        })
        .catch({});
    }
  };

  return (
    <>
      <UserMemberContainerWrapper
        customStyle={{
          border: authorized[invalidateAccess] ? "none" : "0.5px solid #cacaca",
        }}
      >
        {authorized[invalidateAccess] ? (
          <UserMemberContentWrapper>
            <div className={style.fm_search_full}>
              <section className={style.fm_search_full_container}>
                <div className={style.fm_search_full_container_inner}>
                  <img
                    className={style.fm_search_full_icon}
                    alt="search icon"
                    src={`${assestsNavbarUrl}/navbar-search.svg`}
                  />
                  <input
                    type="text"
                    placeholder={t("search")}
                    onChange={onSearchEvent}
                    onKeyUp={onCallBindSearch}
                    value={searchCopy}
                  />
                </div>
              </section>

              {whichFlow === "ADMISSION" ? (
                <CreateButton
                  label="export"
                  customStyle={{
                    margin: "0",
                    padding: "0.4rem 1rem",
                    width: "fit-content",
                  }}
                  onAction={onExportList}
                />
              ) : null}
            </div>
          </UserMemberContentWrapper>
        ) : (
          <>
            <UserMemberContentWrapper>
              <div className={style.fm_header_container}>
                <h6>{t("all_students")}</h6>
                <div className={style.fm_both_filter}>
                  <div className={style.fm_search}>
                    <section className={style.fm_search_container}>
                      <div className={style.fm_search_container_inner}>
                        <img
                          className={style.fm_search_icon}
                          alt="search icon"
                          src={`${assestsNavbarUrl}/navbar-search.svg`}
                        />
                        <input
                          type="text"
                          placeholder={t("search")}
                          onChange={onSearchEvent}
                          style={{
                            minWidth: "300px",
                          }}
                          onKeyUp={onCallBindSearch}
                          value={searchCopy}
                        />
                      </div>
                    </section>
                  </div>
                  {/* <img
                    src={`${assestsUserFinanceUrl}/history.svg`}
                    alt="history icon"
                    onClick={onNavigate}
                  />
                  <CreateButton
                    label="student_message"
                    customStyle={{
                      margin: "0",
                    }}
                    onAction={onToggle}
                  /> */}
                </div>
              </div>
            </UserMemberContentWrapper>
            <BorderBottom />
          </>
        )}

        <UserMemberContentWrapper>
          <FinanceAllStudentTableHeader studentAlias={studentAlias} />
        </UserMemberContentWrapper>

        <BorderBottom />
        <UserMemberContentWrapper>
          {instituteAllStudent?.list?.map((student, index) =>
            instituteAllStudent?.list?.length === index + 1 ? (
              <div key={student?._id} ref={ref}>
                <FinanceAllStudentCard
                  student={student}
                  instituteId={instituteId}
                  messageId={messageId}
                  headId={headId}
                  allStudentControl={allStudentControl}
                />
              </div>
            ) : (
              <FinanceAllStudentCard
                student={student}
                key={student?._id}
                instituteId={instituteId}
                messageId={messageId}
                headId={headId}
                allStudentControl={allStudentControl}
              />
            )
          )}
          {instituteAllStudentLoading ? (
            search ? (
              <QvipleLoader />
            ) : (
              <>{page === 1 ? <QvipleLoader /> : <Loading />}</>
            )
          ) : search ? (
            instituteAllStudent?.list?.length === 0 && (
              <EmptyMessage title="No student found related this search." />
            )
          ) : (
            instituteAllStudent?.list?.length === 0 && (
              <EmptyMessage title="No students added in institute." />
            )
          )}
        </UserMemberContentWrapper>
      </UserMemberContainerWrapper>

      {disabled && <QvipleLoader />}
      {/* {open && (
        <AllStudentMessageSend
          onClose={onToggle}
          messageId={messageId}
          instituteId={instituteId}
          headId={headId}
          parentState={getQuery.state}
          toRedirectUrl={`/q/${params.username}/member/staff/${subUrl}?type=message`}
          accessAs={accessAs}
        />
      )} */}
    </>
  );
};

export default FinanceAllStudent;

const authorized = {
  unAuthorized: true,
  govt: true,
};
